import React from 'react'
import styled from 'styled-components'

const FooterWrapper = styled.div`
    width: 100%;
    height: 100px;
    background: #2d3436;
    position: relative;
`

const Footer = props => {
    return (
        <FooterWrapper></FooterWrapper>
    )
}

export default Footer


